@use "global"as *;

.sub-aboutUs {
  position: relative;
}

.sub-aboutUs__inner {
  max-width: my-rem(600);
  width: 100%;
  position: relative;
}

.sub-aboutUs__decoration {
  position: absolute;
  top: my-rem(-25);
  left: my-rem(15);
  width: my-rem(120);
  height: my-rem(55);
  z-index: -1;

  @include mq(md) {
    top: my-rem(-71);
    width: my-rem(301);
    height: my-rem(138);
    left: calc(50% - min(47.9166vw, 38.75rem));
  }
}

.sub-aboutUs__image-sky {
  position: relative;
  z-index: 2;
  width: 100%;
  display: none;

  @include mq(md) {
    display: block;
    max-width: calc((399 / 1080) * 100%);
  }
}

.sub-aboutUs__image-sky::before {
  content: "";
  display: block;
  padding-top: calc((606 / 399) * 100%);
}

.sub-aboutUs__image-sky img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}



.sub-aboutUs__image-sea {
  position: relative;
  width: 100%;
  max-width: 100%;
  
  @include mq(md) {
    z-index: -1;
    margin-top: -49%;
    margin-left: auto;
    max-width: calc((880 / 1080) * 100%);
  }
}

.sub-aboutUs__image-sea::before {
  content: "";
  display: block;
  padding-top: calc((581 / 880) * 100%);
}

.sub-aboutUs__image-sea img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.sub-aboutUs__container {
  display: flex;
  flex-direction: column;
  margin-top: my-rem(40);
  position: relative;

  @include mq(md) {
    margin-top: initial;
    flex-direction: row;
    position: absolute;
    top: calc((200 / 661) * 100%);
    right: calc((63 / 1080) * 100%);
    max-width: my-rem(840);
    width: 100%;
    z-index: 3;
  }
}

.sub-aboutUs__title-block {
  position: absolute;
  top: my-rem(-245);
  left: my-rem(24);

  @include mq(md) {
    position: initial;
    top: initial;
    left: initial;
    max-width: calc((400 / 840) * 100%);
    width: 100%;
  }
}

.sub-aboutUs__sub-title {
  font-size: my-rem(40);
  font-family: $en-font-family1;
  font-weight: $regular;
  line-height: calc(40 / 40);
  color: $white;

  @include mq(md) {
    font-size: my-rem(80);
    line-height: calc(100 / 80);
  }
}

.sub-aboutUs__meta {
  @include mq(md) {
    margin-top: my-rem(48);
    max-width: calc((400 / 840) * 100%);
    width: 100%;
    margin-left: my-rem(40);
  }
}

.sub-aboutUs__text {
  @include mq(md) {
    color: $white;
  }
}